@import url(https://fonts.googleapis.com/css2?family=Krona+One&display=swap);
*{margin:0;padding:0;box-sizing:border-box;font-family:"Krona One", sans-serif}body{display:flex;justify-content:center;align-items:flex-start;background-image:linear-gradient(to top, #caeaff, #ceecff, #d3eeff, #d7efff, #dbf1ff);height:100vh;padding-top:2rem}.btn{min-width:120px;cursor:pointer;background-color:#e4f5ff;border-radius:0.3rem;padding:0.4rem 0.9rem;border-width:2px;transition:background-color 0.2s}.btn:hover{background-color:#d1eaf9}.btn.delete{font-size:1.5rem;border:none;background-color:inherit}h1{margin:1.6rem 0}h3,h4{margin:0.7rem 0}p{margin-bottom:0.3rem}.app{padding:2rem;width:85vw;max-width:800px;background-color:rgba(255,255,255,0.15);border-radius:1rem;box-shadow:0 1px 2px rgba(0,0,0,0.07),0 2px 4px rgba(0,0,0,0.07),0 4px 8px rgba(0,0,0,0.07),0 8px 16px rgba(0,0,0,0.07),0 16px 32px rgba(0,0,0,0.07),0 32px 64px rgba(0,0,0,0.07)}.app .wrapper{display:flex;justify-content:space-between;flex-wrap:wrap;padding:1rem 0}input{margin:0.5rem 0.5rem 0.5rem 0;padding:0.38rem;border-radius:0.3rem;background-color:#e4f5ff}label{display:block;margin:0}.transaction-item{box-shadow:inset 0px 0px 10px 0px rgba(0,0,0,0.2);background-color:#e4f5ff;list-style:none;border-radius:1rem;padding:1rem;position:relative}.transaction-item .btn{position:absolute;right:16px;top:50%;transform:translateY(-50%)}.highest-transaction-wrapper{padding-top:5px}li{border:2px solid black;margin:1rem 0}.important{color:#f03a17;-webkit-text-stroke:0.5px black;font-size:1.5rem}

